@import '../mixins.scss';

.internalStory {
  padding-top: 98px;
  color: #000;
  padding: 110px 80px 0 80px;
  @media (max-width: 767px) {
    padding: 110px 40px 0 40px;
  }
  &__title {
    font-size: 32px;
    line-height: 1.1;
    margin-bottom: 10px;
    font-weight: 700;
    @media (max-width: 767px) {
      font-size: 26px;
    }
  }
  &__suptitle {
    font-size: 22px;
    line-height: 1.3;
    margin-bottom: 15px;
    max-width: 1100px;
  }
  &__text {
    font-size: 20px;
    line-height: 1.3;
    padding-top: 20px;
    @media (max-width: 998.98px) {
      font-size: 18px;
    }
  }
  &__image {
    img {
      object-fit: cover;
      @media (max-width: 998.98px) {
        width: 450px;
        height: 400px;
      }
      @media (max-width: 550px) {
        width: 300px;
        height: 300px;
      }
      @media (max-width: 450px) {
        width: 250px;
        height: 300px;
      }
    }
  }
  &__info {
    margin-bottom: 15px;
    @include flexbox();
    font-size: 16px;
  }
  &__author {
    span {
      color: #b80000;
      font-weight: 700;
    }
    padding-right: 20px;
  }
  &__views {
    padding-right: 20px;
    span {
      font-weight: 700;
      color: #b80000;
    }
  }
  &__category {
    span {
      font-weight: 700;
      color: #b80000;
    }
  }
}
